body {
  background-color: #f5f5f5;
  margin: 0;
  font-family: Arial, sans-serif;
}

nav {
  z-index: 1000;
  background-color: #1c1c1c;
  justify-content: space-between;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

nav button {
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: none;
  padding: 0;
  font-size: 18px;
}

nav button:hover {
  text-decoration: underline;
}

section {
  grid-template-columns: repeat(4, minmax(300px, 1fr));
  gap: 30px;
  display: grid;
}

.grid-container {
  max-width: 1400px;
  background-color: #fff;
  margin: 0 auto;
  padding: 30px;
}

article {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

article img {
  width: 250px;
  height: 250px;
  object-fit: contain;
}

article p {
  margin: 0 0 15px;
  font-size: 16px;
}

article button {
  color: #fff;
  cursor: pointer;
  background-color: #1c1c1c;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
}

article button:hover {
  background-color: #383838;
}

article h3 {
  flex-grow: 1;
  margin: 0 0 10px;
  font-size: 18px;
  font-weight: 500;
}

.product-button {
  color: #fff;
  cursor: pointer;
  background-color: #1c1c1c;
  border: none;
  border-radius: 10%;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
}

button:hover {
  background-color: #383838;
}

@media (max-width: 1200px) {
  section {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
}

@media (max-width: 1000px) {
  section {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  }
}

@media (max-width: 768px) {
  section {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  }
}

@media (max-width: 480px) {
  section {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

.cart-container {
  max-width: 1250px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 80px auto;
  display: flex;
}

.cart-header {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.cart-title {
  text-align: center;
  margin-bottom: 30px;
}

.cart-items-container {
  width: 100%;
  flex-flow: wrap;
  gap: 10px;
  display: flex;
}

.cart-item {
  width: 150px;
  height: 250px;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  margin-bottom: 30px;
  display: flex;
  box-shadow: 0 2px 4px #0003;
}

.cart-item-image {
  width: 150px;
  height: 150px;
  overflow: hidden;
}

.cart-item-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.cart-item-details {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  padding: 20px;
  display: flex;
}

.cart-item-name {
  margin-bottom: 5px;
  font-size: 18px;
  font-weight: 500;
}

.cart-item-price {
  margin: 0;
  font-size: 16px;
}

.cart-item-quantity {
  margin: 0;
  font-size: 16px;
  font-weight: bold;
}

.cart-summary {
  background-color: #f5f5f5;
  border-radius: 5px;
  justify-content: flex-start;
  align-items: center;
  margin-top: 50px;
  padding: 20px 20px 20px 1px;
  display: flex;
}

.cart-total {
  margin: 0;
  font-size: 20px;
  font-weight: bold;
}

.cart-buttons {
  justify-content: center;
  margin-right: 3.5%;
  display: flex;
}

.cart-buttons button {
  color: #fff;
  cursor: pointer;
  background-color: #1c1c1c;
  border: none;
  border-radius: 10%;
  margin-left: 20px;
  padding: 10px 20px;
  font-size: 16px;
  display: flex;
}

.cart-buttons button:hover {
  background-color: #383838;
}

@media (max-width: 768px) {
  .cart-item {
    width: 100%;
  }

  .cart-buttons {
    justify-content: space-between;
    margin-right: 0;
  }
}

.filter-container {
  max-width: 1475px;
  margin: 80px auto 0;
}

.filters {
  background-color: #f5f5f5;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.filters label {
  margin-left: 10px;
  font-size: 16px;
}

.filters select, .filters input {
  margin-left: 12px;
  padding: 5px;
  font-size: 16px;
}

@media (max-width: 1024px) {
  .filters {
    justify-content: space-around;
  }
}

@media (max-width: 768px) {
  .filters {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  .filters label, .filters select, .filters input {
    margin-left: 0;
  }
}

@media (max-width: 480px) {
  .filter-container {
    margin: 40px auto 0;
  }

  .filters {
    padding: 10px;
  }
}

